<script>
/* eslint-disable */
// import { mapState } from 'vuex';
import Header from './Header.vue';
import { Editor, EditorContent, EditorMenuBar, EditorMenuBubble } from 'tiptap';
import {
  Blockquote,
  // CodeBlock,
  HardBreak,
  Heading,
  HorizontalRule,
  OrderedList,
  BulletList,
  ListItem,
  TodoItem,
  TodoList,
  Bold,
  // Code,
  Italic,
  Link,
  Strike,
  Underline,
  History,
  Focus
} from 'tiptap-extensions';

export default {
  name: 'TextEditor',
  components: {
    EditorContent,
    EditorMenuBar,
    EditorMenuBubble,
    Header
  },
  data() {
    return {
      editor: new Editor({
        extensions: [
          new Blockquote(),
          new BulletList(),
          // new CodeBlock(),
          new HardBreak(),
          new Heading({ levels: [1, 2, 3] }),
          new HorizontalRule(),
          new ListItem(),
          new OrderedList(),
          new TodoItem(),
          new TodoList(),
          new Link(),
          new Bold(),
          // new Code(),
          new Italic(),
          new Strike(),
          new Underline(),
          new History(),
          new Focus({
            className: 'has-focus',
            nested: true
          })
        ],
        autoFocus: true,
        onUpdate: ({ getHTML, getJSON }) => {
          this.textHTML = getHTML();
          // this.textJSON = getJSON();
        }
      }),
      linkUrl: null,
      linkMenuIsActive: true
    };
  },
  computed: {
    /*    ...mapState({
      textHTML: state => state.textHTML
    }), */
    /*    textJSON: {
      get() {
        return this.$store.state.textJSON;
      },
      set(value) {
        this.$store.commit('setTextJSON', value);
      }
    }, */
    textHTML: {
      get() {
        return this.$store.state.textHTML;
      },
      set(value) {
        this.$store.commit('setTextHTML', value);
      }
    }
  },
  methods: {
    showModal() {
      this.$modal.show('faq');
    },
    hideModal() {
      this.$modal.hide('faq');
    },
    showLinkMenu(attrs) {
      this.linkUrl = attrs.href;
      this.linkMenuIsActive = true;
      this.$nextTick(() => {
        this.$refs.linkInput.focus();
      });
    },
    hideLinkMenu() {
      this.linkUrl = null;
      this.linkMenuIsActive = false;
    },
    setLinkUrl(command, url) {
      command({ href: url });
      this.hideLinkMenu();
    }
  },
  mounted() {
    this.editor.setContent(this.textHTML);
  },
  beforeDestroy() {
    this.editor.destroy();
  }
};
</script>

